import React from 'react'
import {NavLink,Switch,Redirect,withRouter} from  'react-router-dom'
import RouteWithSubRoutes from './RouteWithSubRoutes'
import './doc.css'
const Docu = ({routes}) => {
    return (
         <div className='doc'>
            <div className='content'>
                <Switch>
                    {routes.map((route, i) => (<RouteWithSubRoutes key={i} {...route} />))} 
                </Switch>
                <Redirect from='/docu' to='/docu/core'/>
            </div>
            <ol>
                <li><NavLink to='/docu/core'>核心概念</NavLink></li>
                <li><NavLink to='/docu/guide'>高级指引</NavLink></li>
                <li><NavLink to='/docu/api'>API</NavLink></li>
                <li><NavLink to='/docu/hooks'>HOOKS</NavLink></li>
            </ol>
        </div>
    )
}
export default withRouter(Docu)