import Commu from './Commu'
import Home from './Home'
import Login from './Login'
import Docu from './Docu'
import Teach from './Teach'
import Blog from './Blog'
import Core from './Core'
import Guide from './Guide'
import Api from './Api'
import Hooks from './Hooks'

//路由添加
const routes=[
    {
        path:'/',
        exact:true,
        component:Home
    },
    {
        path:'/docu',
        component:Docu,
        pr:true,
        routes:[
            {path:'/docu/core',component:Core},
            {path:'/docu/guide',component:Guide},
            {path:'/docu/api',component:Api},
            {psth:'/docu/hooks',component:Hooks}
        ]
    },
    {
        path:'/teach',
        component:Teach,
        pr:true
    },
    {
        path:'/commu',
        component:Commu,
    },
    {
        path:'/blog',
        component:Blog,
    },
    {
        path:'/login',
        component:Login
    }
]
export default routes